form {
    width: 90%;
    max-width: 300px;
    background-color: #fafbfb;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'Montserrat';
    border-radius: 15px;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1a1c22;
    margin-bottom: 20px;
  }
  
  textarea {
    background-color: #cacecf;
    border: 2px solid #202329;
    border-radius: 12px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::placeholder {
    color: #4a5254;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #6180fa;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button--add {
    background: #1ec4f2;
    box-shadow: 0px 5px 25px rgba(97, 217, 250, 0.859);
    border-radius: 10px;
    color: wheat;
  }
  
  .TodoForm-button--cancel {
    background: red;
    color: white;
    border-radius: 10px;
    border: solid 1.5px red;
  }